/* src/styles/common.css */
.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 20px auto;
  max-width: 1200px;
  width: 100%;
  padding: 0 20px;
  
}

.title {
  font-size: 2.2em;
  font-weight: bold;
  color: #333;
  margin-bottom: 20px;
  text-align: center;
  width: 100%;
}

.button {
  align-self: center;
  margin-top: 20px;
  padding: 10px 20px;
  font-size: 1.2em;
  background-color: #007bff;
  color: white;
  font-family: 'pridefont';
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.button:hover {
  background-color: #0056b3;
} 

body {
  background: linear-gradient(135deg, #c8e1f5, #ffffff);
}


