/* src/styles/SubjectCombination.css */
.timetable-title {
    text-align: center;
    margin-bottom: 40px;
    font-family: 'nanumsquareroundeb';
}
  

.optimal-subjects h3 {
  font-family: 'nanumsquareroundeb';
}

.optimal-subjects li {
  font-family: 'nanumsquareroundr';
}

.optimal-subjects p {
  font-family: 'nanumsquareroundeb';
  font-weight: bold;
}

.button-container {
  display: flex;
  justify-content: center;
  width: 100%;
  margin-top: 20px;
}

.timetable-header {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 2rem;
}

.timetable-header h3 {
  font-size: 1.5em;
  margin: 0;
  text-align: center;
}

.button {
  background-color: rgba(0, 100, 255, 0.9);
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1em;
  transition: background-color 0.3s ease;
}

.button:hover {
  background-color: rgba(0, 100, 255, 0.7);
}

.timetable-wrapper {
  margin-bottom: 80px;
}

.button-group {
  display: flex;
  gap: 10px;
  justify-content: center;
  margin-top: 20px;
}

.button {
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
  background-color: #007bff;
  color: white;
  cursor: pointer;
  transition: background-color 0.2s;
}

.button:hover {
  background-color: #0056b3;
}
  
.reset-icon {
  display: inline-block;
  transition: transform 0.3s ease;
}

.button:hover .reset-icon {
  transform: rotate(-180deg);
}

/* 박스 스타일 추가 */
.optimal-subjects-box {
  background-color: white; /* 박스 배경색 */
  border: 2px solid rgba(0, 123, 255, 0.3); /* 테두리 색 */
  border-radius: 10px; /* 둥근 모서리 */
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* 그림자 */
  padding: 20px; /* 내부 여백 */
  margin: 20px auto; /* 위아래 간격 및 가운데 정렬 */
  width: 80%; /* 박스 너비 */
  text-align: center; /* 텍스트 정렬 */
}

/* 한 열로 나누는 스타일 */
.optimal-subjects-list {
  display: block; /* Flexbox 사용하지 않음 */
  padding: 0; /* 기본 여백 제거 */
  margin: 0; /* 기본 여백 제거 */
  list-style: none; /* 기본 점 제거 */
}

.optimal-subject-item {
  margin-bottom: 10px; /* 항목 간격 */
  font-family: 'nanumsquareroundr'; /* 텍스트 스타일 */
  font-size: 1em;
  line-height: 1.5;
}

/* 반응형 디자인 추가 */
@media (max-width: 768px) {
  .optimal-subjects-box {
    width: 95%; /* 모바일에서는 더 넓게 */
    padding: 15px;
  }

  .optimal-subject-item {
    font-size: 0.9em;
  }

  .button-group {
    flex-direction: column;
    gap: 15px;
  }

  .button {
    width: 100%;
    padding: 12px 20px;
  }

  .timetable-title {
    font-size: 1.8em;
  }
}
