/* src/styles/CustomTimetable.css */
.custom-timetable-container {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  padding: 1rem;
  min-height: 100vh;
}

.custom-timetable-container p {
  margin: 0;
  font-weight: bold;
}

.custom-timetable-title {
  font-family: 'nanumsquareroundeb';
}

.description {
  align-self: center;
}

.timetable-content {
  display: flex;
  width: 80%;
  align-self: center;
  gap: 2rem;
  flex: 1;
}

.subject-list-container {
  font-family: 'nanumsquareroundr';
  width: 250px;
  background: #f5f5f5;
  padding: 1rem;
  border-radius: 8px;
}

.subject-list {  
  height: 500px;
  overflow-y: auto;
  padding: 0.5rem;
}

.subject-item {  
  background: white;
  padding: 0.8rem;
  margin: 0.5rem 0;
  border-radius: 4px;
  border: 1px solid #ddd;
  cursor: grab;
  transition: all 0.3s ease;
}

.subject-item:hover {
  background: #f8f8f8;
  transform: translateY(-2px);
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.custom-timetable {
  flex-grow: 1;
  font-family: 'nanumsquareroundr';
  border: 1px solid #ddd;
  border-radius: 8px;
}

.custom-timetable-header {
  display: grid;
  grid-template-columns: 80px repeat(5, 1fr);
  background: #f5f5f5;
  border-bottom: 1px solid #ddd;
}

.custom-timetable-row {
  display: grid;
  grid-template-columns: 80px repeat(5, 1fr);
}

.custom-time-cell, .custom-day-cell {
  padding: 0.8rem;
  text-align: center;
  border-right: 1px solid #ddd;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
}

.custom-timetable-cell {
  min-height: 60px;
  border-right: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
  padding: 0.5rem;
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: center;
  background: white;
}

.custom-timetable-cell.disabled {
  background: #f5f5f5;
  cursor: not-allowed;
}

.subject-item.dragging {
  opacity: 0.5;
}

.custom-timetable-cell.drag-over {
  background-color: #e0f7fa;
  border: 1px dashed #007bff;
}

@keyframes dropAnimation {
  0% {
    transform: scale(1.1);
    background-color: #e3f2fd;
  }
  50% {
    transform: scale(0.95);
  }
  100% {
    transform: scale(1);
    background-color: white;
  }
}

.custom-timetable-cell.drop-animation {
  animation: dropAnimation 0.5s ease-out forwards;
}

.button-group {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-top: 2rem;
  padding: 1rem;
}

.button-group .button {
  min-width: 120px;
}

@keyframes resetAnimation {
  0% {
    transform: translate(0, 0) scale(1);
    opacity: 1;
  }
  100% {
    transform: translate(0, 0) scale(0.5);
    opacity: 0;
  }
}

.custom-timetable-cell.reset-animation {
  animation: resetAnimation 0.5s ease-out forwards;
}

@keyframes listHighlight {
  0% {
    background-color: #f5f5f5;
  }
  50% {
    background-color: #e3f2fd;
  }
  100% {
    background-color: #f5f5f5;
  }
}

.subject-list-container.reset-highlight {
  animation: listHighlight 0.8s ease-out forwards;
}

@keyframes returnHighlight {
  0% {
    background-color: white;    
    transform: scale(1);
  }
  50% {
    background-color: #e3f2fd;    
    transform: scale(1.05);
  }
  100% {
    background-color: white;    
    transform: scale(1);
  }
}

.subject-item.return-highlight {
  animation: returnHighlight 0.8s ease-out forwards;
}

.subject-list-header {
  font-family: 'nanumsquareroundr';
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

.subject-list-header h3 {
  margin: 0;
}

.random-button {
  font-family: 'pridefont';
  background-color: #007bff;
  color: white;
  border: none;
  margin-top: 0px;
  padding: 0.3rem 0.8rem;
  border-radius: 4px;
  cursor: pointer;
  font-size: 0.9rem;
  transition: background-color 0.3s ease;
}

.random-button:hover {
  background-color: #0056b3;
}

.custom-timetable-cell.drop-animation {
  animation: dropAnimation 0.5s ease-out forwards;  
}

/* 반응형 디자인 보강 */
@media (max-width: 768px) {
  .timetable-content {
    flex-direction: column;
    width: 95%;
  }

  .subject-list-container {
    width: 100%;
    height: auto;
    margin-bottom: 20px;
  }

  .subject-list {
    height: 300px;
  }

  .custom-time-cell, .custom-day-cell {
    padding: 0.5rem;
    font-size: 0.9em;
  }

  .custom-timetable-cell {
    min-height: 50px;
    font-size: 0.9em;
  }

  .button-group {
    flex-direction: column;
  }

  .button {
    width: 100%;
  }
}
