/* src/styles/SavedTimeTable.css */
.saved-page-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2rem;
  gap: 1rem;
}

.saved-page-header {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 80%;
  margin-bottom: 20px;
}

.saved-page-title {
  color: #333;
  text-align: center;
  margin: 10px 0;
  font-family: 'nanumsquareroundeb';
  font-size: 2.8em;
}

.saved-page-content {
  display: flex;
  gap: 20px;
  width: 80%;
  margin-top: 20px;
}

.saved-description {    
    margin-bottom: 20px;
}

.saved-list-container {
  flex: 1;
  max-width: 300px;
  padding: 10px;
  background-color: #f5f5f5;
  border-radius: 10px;
  height: 500px;
  overflow: hidden;
}

.saved-list {
  height: 100%;
  overflow-y: auto;
  padding-right: 10px;
}

.saved-list::-webkit-scrollbar {
  width: 8px;
}

.saved-list::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 4px;
}

.saved-list::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 4px;
}

.saved-list::-webkit-scrollbar-thumb:hover {
  background: #555;
}

.saved-list-item {
  padding: 15px;
  background-color: #ffffff;
  border: 1px solid #ddd;
  border-radius: 8px;
  margin-bottom: 10px;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.saved-list-item:hover {  
  background-color: rgba(0, 100, 255, 0.5);
  transform: translateY(-2px);
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.saved-item-selected {
  border-color: #007bff;
  background-color: rgba(0, 100, 255, 0.5);
}

.saved-item-info h3 {
  margin: 0;
  font-size: 16px;
  font-family: 'nanumsquareroundeb';
}

.saved-item-date {
  margin: 5px 0 0;
  font-size: 12px;
  color: #666;
}

.saved-delete-button {
  font-family: 'nanumsquareroundr';
  width: 50px;
  height: 25px;
  font-size: 14px;
  padding: 5px 10px;
  background-color: #ff4444;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.saved-delete-button:hover {
  background-color: #cc0000;
}

.saved-detail {
  flex: 2;
}

.saved-detail h3 {
  font-family: 'nanumsquareroundeb';
  color: #333;  
  text-align: center;
  margin-bottom: 20px;
}

.saved-empty-message {
  color: #666;
  text-align: center;
  padding: 20px;
  font-family: 'nanumsquareroundr';
}

.saved-timetable {
  width: 100%;
  max-width: 1000px;
  border: 1px solid #ddd;
  border-radius: 8px;
  overflow: visible;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  font-family: 'nanumsquareroundr';
}

.saved-timetable-header {
  display: grid;
  grid-template-columns: 80px repeat(5, 1fr);
  background: #f5f5f5;
  border-bottom: 1px solid #ddd;
}

.saved-time-cell, .saved-day-cell {
  padding: 0.5rem;
  text-align: center;
  border-right: 1px solid #ddd;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
}

.saved-timetable-row {
  display: grid;
  grid-template-columns: 80px repeat(5, 1fr);
}

.saved-timetable-cell {
  position: relative;
  min-height: 40px;
  border-right: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
  padding: 0.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  background: white;
  transition: all 0.3s ease;
}

.saved-cell-disabled {
  background: #f5f5f5;
  color: #999;
}

@media (max-width: 768px) {
  .saved-timetable {
    width: 95%;
  }

  .saved-time-cell, .saved-day-cell, .saved-timetable-cell {
    padding: 0.5rem;
    font-size: 0.9em;
  }
} 

.button-group {
  margin-top: 2rem;
  display: flex;
  justify-content: center;
  width: 100%;
}

.saved-button-group {
  margin-top: 1rem;
  margin-bottom: 2rem;
}
