/* src/styles/Survey.css */
.survey-container {
    overflow-y: auto;  
    animation: fadeIn 1s ease-in-out;  
}

.survey-title {
    text-align: center;
    font-family: 'nanumsquareroundb';
}

form {
    display: flex;
    flex-direction: column;
    gap: 30px; /* 각 질문 간의 간격 증가 */
    align-items: center; /* flex-start에서 center로 변경 */
    width: 100%; /* 너비 100%로 설정 */
}
  
.survey-question {
    margin-bottom: 30px; /* 질문과 옵션 사이 간격 증가 */
    display: flex;
    flex-direction: column;
    align-items: center; /* 가운데 정렬 */
    width: 80%;
    padding-bottom: 150px;
    border-bottom: 2px solid #ccc;
    opacity: 1;
    transition: opacity 0.3s ease;
}

.survey-question.answered {
    opacity: 0.7;
}
  
.survey-label {
    font-family: 'nanumsquareroundb';
    font-size: 2em; /* 텍스트 크기 */
    margin-bottom: 120px; /* 옵션과의 간격 증가 */
    color: #555;
    text-align: center; /* 텍스트 가운데 정렬 */
}
  
.survey-options {
    display: flex;
    gap: 100px; /* 옵션 간의 간격 */
    justify-content: center; /* 중앙 정렬 추가 */
    flex-wrap: wrap; /* 반응형을 위한 wrap 추가 */
}
  
.survey-option {
    position: relative;
    text-align: center;
}
  
.survey-option input[type="radio"]{
    appearance: none;
    -webkit-appearance: none;
    width: 50px;
    height: 50px;
    border: 2px solid #ccc;
    border-radius: 50%;    
    font-size: 1.2em;
    font-weight: bold;    
    color: transparent;
    background-color: #fff;
    cursor: pointer;
    transition: all 0.3s ease;
}
  
.survey-option input[type="radio"]::before {
    content: attr(value); 
    position: absolute;
    top: 50%;
    left: 45%;
    transform: translate(-25%, -50%);    
    font-size: 1.5em;
    color: rgba(0, 0, 0, 0.2);
}

.survey-option input[type="radio"]:hover{
    border-color: rgba(0, 100, 255, 1);
}

.survey-option input[type="radio"]:checked{
    background-color: rgba(0, 100, 255, 0.9);
    color: #fff;
    border-color: rgba(0, 100, 255, 0.6);
    box-shadow: 0 0 8px rgba(0, 100, 255, 1);
}

button {
    margin-top: 20px;
    padding: 10px 20px;
    font-size: 1.2em;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: all 0.3s ease;
}
  
button:hover {
    background-color: #0056b3;
}

.button .arrow-right {
  display: inline-block;
  transition: transform 0.3s ease;
}

.button:hover .arrow-right {
  transform: translateX(10px);
}

.button .arrow-left {
  display: inline-block;
  transition: transform 0.3s ease;
}

.button:hover .arrow-left {
  transform: translateX(-10px);
}

.subtitle {
  font-size: 1.8em;
  font-family: 'nanumsquareroundb';
  color: #444;
  margin-bottom: 15px;
  text-align: center;
  width: 100%;
}

.description {  
  padding-bottom: 50px;
  border-bottom: 2px solid #ccc;  
  margin-bottom: 70px;
  font-family: 'nanumsquareroundb';
  font-size: 1.2em;
  color: #666;
  text-align: center;
  width: 80%;
}

.button-group {
  display: flex;
  gap: 20px;
  justify-content: center;
  margin-top: 40px;
  margin-bottom: 100px;
}

.button-group .button {
  min-width: 120px;
}

.choice-options {
  display: flex;
  flex-direction: column;
  gap: 15px;
  width: 100%;
  max-width: 600px;
  margin: 0;
}

.choice-option {
  font-family: 'nanumsquareroundb';
  padding: 20px;
  background-color: white;
  border: 2px solid #ccc;
  border-radius: 10px;
  cursor: pointer;
  transition: all 0.3s ease;
  text-align: center;
  font-size: 1.2em;
  color: #555;
}

.choice-option:hover {
  border-color: rgba(0, 100, 255, 0.6);
  transform: translateY(-2px);
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.choice-option.selected {
  background-color: rgba(0, 100, 255, 0.9);  
  color: white;
  border-color: rgba(0, 100, 255, 0.6);
  box-shadow: 0 0 8px rgba(0, 100, 255, 1);
}


@keyframes bounce {
  0%, 50%, 100% {
    transform: translateY(0);
  }
  25% {
    transform: translateY(-10px);
  }
  75% {
    transform: translateY(-5px);
  }
}

.submit-icon {
  display: inline-block;
  transition: transform 0.3s ease;
}

.button:hover .submit-icon {
  animation: bounce 1s infinite;
}

.survey-progress-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: fixed; 
  bottom: 0;    
  width: 60%;
  z-index: 1000;
}

.survey-progress {
  width: 100%;
  appearance: none;
  -webkit-appearance: none;
}


.survey-progress::-webkit-progress-bar {
  background-color: #f0f0f0;
  border-radius: 10px;
  height: 10px;
}


.survey-progress::-webkit-progress-value {
    background: linear-gradient(90deg, #007bff, #00d4ff);
  border-radius: 10px;
  transition: width 0.5s ease-in-out;
}


.survey-progress::-moz-progress-bar {
  background-color: rgba(0, 100, 255, 0.9);
  border-radius: 10px;
  transition: width 0.5s ease-in-out;
}


.survey-progress-text {
  font-family: 'nanumsquareroundb';  
  color: black;  
  transition: all 0.5s ease-in-out;
}

@keyframes fadeIn {
  from {
      opacity: 0;
      transform: translateY(10px);
  }
  to {
      opacity: 1;
      transform: translateY(0);
  }
}

/* 질문 박스 스타일 */
.survey-question-box {
  background-color: white; /* 박스 배경색 */
  border: 2px solid rgba(0, 123, 255, 0.3); /* 테두리 색 */
  border-radius: 10px; /* 둥근 모서리 */
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* 그림자 */
  padding: 20px; /* 내부 여백 */
  margin: 20px auto; /* 위아래 간격 및 가운데 정렬 */
  width: 80%; /* 박스 너비 */
  text-align: center; /* 텍스트 정렬 */
  display: flex;      /* Flexbox로 설정 */
  flex-direction: column; /* 세로 방향 정렬 */
  align-items: center; /* 자식 요소 가운데 정렬 */

}

/* 질문 텍스트 스타일 */

.survey-question-box h3 {
  margin-bottom: 15px; /* 간격 */
  color: #555;
}

/* 반응형 디자인 추가 */
@media (max-width: 768px) {
  .survey-container {
    padding: 1rem;
  }

  .survey-question-box {
    width: 95%;
    padding: 15px;
  }

  .survey-label {
    font-size: 1.6em;
    margin-bottom: 80px;
  }

  .survey-options {
    gap: 50px;
  }

  .survey-option input[type="radio"] {
    width: 40px;
    height: 40px;
  }

  .survey-option input[type="radio"]::before {
    font-size: 1.2em;
  }

  .button-group {
    flex-direction: column;
    gap: 15px;
  }

  .button {
    width: 100%;
  }

  .survey-progress-container {
    width: 90%;
  }
}
