/* src/styles/StartPage.css */
.start-page-container {
  min-height: 100vh;
  padding: 40px 20px;
  background: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);
  font-family: 'nanumsquareroundr', sans-serif;
}

.start-content {
  max-width: 1200px;
  margin: 0 auto;
  background: white;
  border-radius: 20px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  padding: 40px;
}

.icon-container {
  text-align: center;
  margin-bottom: 30px;
}

.icon-circle {
  width: 80px;
  height: 80px;
  background: #4a90e2;
  border-radius: 50%;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 20px;
}

.material-icons {
  font-size: 40px;
  color: white;
}

.main-title {
  text-align: center;
  font-size: 2.5em;
  color: #2c3e50;
  margin-bottom: 50px;
}

.feature-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 30px;
  margin-bottom: 50px;
}

.feature-item {
  font-family: 'nanumsquareroundr', sans-serif;
  text-align: center;
  padding: 30px;
  background: #f8f9fa;
  border-radius: 15px;
  transition: transform 0.3s ease;
}

.feature-item:hover {
  transform: translateY(-5px);
}

.feature-icon {
  font-size: 2.5em;
  margin-bottom: 15px;
}

.feature-item h3 {
  color: #2c3e50;
  margin-bottom: 10px;
}

.feature-item p {
  color: #666;
  line-height: 1.6;
}

.button-container {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-top: 40px;
}

.primary-button {
  background: #4a90e2;
  padding: 15px 30px;
  font-size: 1.2em;
  display: flex;
  align-items: center;
  gap: 10px;
}

.arrow {
  transition: transform 0.3s ease;
}

.primary-button:hover .arrow {
  transform: translateX(5px);
}

.info-button {
  background: #6c757d;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  gap: 10px;
}

.info-button.active {
  background-color: #0f559b;
}

.arrow-down {
  display: inline-block;
  transition: transform 0.3s ease;
}

.info-button:hover .arrow-down {
  transform: translateY(5px);
}

.info-button.active .arrow-down {
  transform: rotate(180deg) !important;
}

.info-button.active:hover .arrow-down {
  transform: rotate(180deg) translateY(5px) !important;
}

html {
    scroll-behavior: smooth;
}

.info-section {
  margin-top: 50px;
  padding-top: 40px;
  border-top: 1px solid #eee;
  transition: all 0.3s ease;
  max-height: 0;
  overflow: hidden;
  opacity: 0;
}

.info-section.active {
  max-height: 1000px;
  opacity: 1;
}

.info-card {
  background: #f8f9fa;
  border-radius: 15px;
  padding: 30px;
}

.info-content {
  display: grid;
  gap: 20px;
  margin-top: 20px;
}

.info-item {
  display: flex;
  align-items: flex-start;
  gap: 15px;
}

.info-icon {
  font-size: 1.5em;
  flex-shrink: 0;
}

.highlight-text {
  color: #4a90e2;
  font-weight: bold;
  text-decoration: underline;
  cursor: help;
  position: relative;
}

.highlight-text:hover::after {
  content: attr(data-tooltip);
  position: absolute;
  bottom: 100%;
  left: 50%;
  transform: translateX(-50%);
  background-color: rgba(0, 0, 0, 0.8);
  color: white;
  padding: 10px 15px;
  border-radius: 6px;
  font-size: 0.9em;
  font-weight: normal;
  width: max-content;
  word-break: keep-all;
  white-space: pre-wrap;
  max-width: 210px;
  z-index: 1000;
  text-decoration: none;
}

.highlight-text:hover::before {
  content: '';
  position: absolute;
  bottom: 100%;
  left: 50%;
  transform: translateX(-50%);
  border: 8px solid transparent;
  border-top-color: rgba(0, 0, 0, 0.8);
  z-index: 1000;
}

@media (max-width: 768px) {
  .feature-grid {
    grid-template-columns: 1fr;
  }
  
  .button-container {
    flex-direction: column;
  }
  
  .main-title {
    font-size: 2em;
  }
}
