/* src/styles/FinalTimetable.css */
.final-timetable-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2rem;
  gap: 2rem;
}

.final-timetable-container h2 {
  color: #333;
  margin: 10px 0;
  font-family: 'nanumsquareroundeb';
}


.final-timetable {  
  font-family: 'nanumsquareroundr';
  width: 80%;
  max-width: 1000px;
  border: 1px solid #ddd;
  border-radius: 8px;
  overflow: visible;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.final-timetable-header {
  display: grid;
  grid-template-columns: 80px repeat(5, 1fr);
  background: #f5f5f5;
  border-bottom: 1px solid #ddd;
}

.final-timetable-row {
  display: grid;
  grid-template-columns: 80px repeat(5, 1fr);
}

.final-time-cell, .final-day-cell {
  padding: 0.5rem;
  text-align: center;
  border-right: 1px solid #ddd;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
}

.final-timetable-cell {
  position: relative;
  min-height: 40px;
  border-right: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
  padding: 0.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  background: white;
  transition: all 0.3s ease;
}

.final-timetable-cell:hover {
  background: #f8f8f8;
  transform: scale(1.05);
}

.final-timetable-cell.disabled {
  background: #f5f5f5;
  color: #999;
}

/* 툴팁 스타일 추가 */
.final-timetable-cell[data-tooltip]:hover::after {
  content: attr(data-tooltip);
  position: absolute;
  bottom: 100%;
  left: 50%;
  transform: translateX(-50%);
  background: rgba(0, 0, 0, 0.8);
  color: white;
  padding: 5px 10px;
  border-radius: 4px;
  font-size: 0.9em;
  white-space: pre;
  z-index: 1000;  
}

/* 모바일 대응 */
@media (max-width: 768px) {
  .final-timetable {
    width: 95%;
  }

  .final-time-cell, .final-day-cell, .final-timetable-cell {
    padding: 0.5rem;
    font-size: 0.9em;
  }

  .final-timetable-cell[data-tooltip]:hover::after {
    font-size: 0.8em;
    padding: 3px 6px;
  }
} 