/* src/styles/Header.css */
@font-face {
  font-family: 'pridefont';
  src: url('../fonts/VITRO\ PRIDE\ TTF.ttf');
  font-weight: bold;
  font-style: normal;
}

@font-face {
  font-family: 'nanumsquareroundl';
  src: url('../fonts/NANUMSQUAREROUNDL.TTF');
  font-weight: medium;
  font-style: normal;
}

@font-face {
  font-family: 'nanumsquareroundr';
  src: url('../fonts/NANUMSQUAREROUNDR.TTF');
  font-weight: medium;
  font-style: normal;
}

@font-face {
  font-family: 'nanumsquareroundb';
  src: url('../fonts/NANUMSQUAREROUNDB.TTF');
  font-weight: medium;
  font-style: normal;
}

@font-face {
  font-family: 'nanumsquareroundeb';
  src: url('../fonts/NANUMSQUAREROUNDEB.TTF');
  font-weight: medium;
  font-style: normal;
}
header {
  margin: 50px auto;
  text-align: center;
  width: 100%;
  max-width: 1200px;
  padding: 0 20px;
}

h1 {
  font-size: 4em;
  font-family: 'nanumsquareroundeb';
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2);
}

.styled-header {
  background: linear-gradient(135deg, #4a90e2, #0066cc);
  color: white;
  padding: 20px 40px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.header-row {
  display: flex;
  align-items: center;
  gap: 20px;
}

.header-icon {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  background-color: white;
  object-fit: cover;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2);
}

.header-text {
  display: flex;
  flex-direction: column;
}

.header-date {
  font-size: 0.9em;
  margin: 0;
  opacity: 0.8;
}

.header-title {
  font-size: 1.8em;
  font-weight: bold;
  margin: 5px 0 0 0;
}

.header-content p {
  margin: 0;
  font-size: 1.2em;
  opacity: 0.8;
}

.header-content h1 {
  font-size: 1.8em;
  font-weight: bold;
  margin: 10px 0;
}

.styled-header::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100px;
  height: 100px;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 50%;
}

.styled-header::after {
  content: '';
  position: absolute;
  top: 50px;
  right: 20px;
  width: 60px;
  height: 60px;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 50%;
}

@media (max-width: 768px) {
  .styled-header {
    padding: 15px 0;
    animation: fadeIn 1s ease-in-out;
  }

  .header-row {
    flex-direction: column;
    align-items: center;
    gap: 10px;
  }

  .header-icon {
    width: 60px;
    height: 60px;
  }

  .header-title {
    font-size: 1.5em;
  }

  .header-content h1 {
    font-size: 1.5em;
  }

  .header-content p {
    font-size: 0.8em;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.header-icon-left {
  position: absolute;
  top: 20px;
  left: 20px;
  width: 60px;
}
